html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  background-color: #9bc5de;
}

.App {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #585858;
}

.nav ul {
  z-index: 6;
  position: absolute;
  top: 50px;
  right: 50px;
}

.nav li {
  padding-right: 40px;
  display: inline-block;
}

.nav p {
  letter-spacing: 3px;
  font-size: 2vh;
  font-weight: bold;
  color: white !important;
  text-decoration: none;
  font-family: "montserrat";
  text-transform: uppercase;
}

.nav p:hover {
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  padding-bottom: 5px;
  border-bottom: 3px solid;
}

.header {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 1000px;
}

.headerimg {
  width: 100%;
  position: absolute;
  height: 1000px;
  -webkit-clip-path: polygon(
    7% 10%,
    53% 10%,
    53% 31%,
    36% 31%,
    36% 86%,
    23% 86%,
    23% 31%,
    7% 31%
  );
  clip-path: polygon(
    7% 10%,
    53% 10%,
    53% 31%,
    36% 31%,
    36% 86%,
    23% 86%,
    23% 31%,
    7% 31%
  );
  background-color: #fff;
}

.headerimg .image-1 {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: auto;
  height: 100px;
  max-width: 80%;
  max-height: 80%;
  margin-left: 15%;
}

.headerimg .image-2 {
  position: relative;
  margin-top: -10px;
  margin-right: auto;
  width: auto;
  height: 210px;
  max-width: 80%;
  max-height: 80%;
  margin-left: 35%;
}
.headerimg .image-3 {
  position: relative;
  margin-top: 10px;
  margin-right: auto;
  width: auto;
  height: 75px;
  max-width: 80%;
  max-height: 80%;
  margin-left: 25%;
}
.headerimg .image-4 {
  position: relative;
  margin-top: 30px;
  margin-right: auto;
  width: auto;
  height: 125px;
  max-width: 80%;
  max-height: 80%;
  margin-left: 30%;
}
.headerimg .image-5 {
  position: relative;
  margin-top: 120px;
  margin-right: auto;
  width: auto;
  height: 60px;
  max-width: 80%;
  max-height: 80%;
  margin-left: 30%;
}
.headerimg .image-6 {
  position: relative;
  margin-top: 100px;
  margin-right: auto;
  width: auto;
  height: 150px;
  max-width: 80%;
  max-height: 80%;
  margin-left: 20%;
}
header {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: auto;
  height: 550px;
  -webkit-transform: translateX(200%);
          transform: translateX(200%);
  animation: slide-in-left 1.8s forwards;
